@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
    color: lightblue;
}

body {
    margin: 0;
    font-family: 'Rubik', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #282c34;
    color: aliceblue;
    max-width: 1100px;
    margin: 0 auto;
}
.App {
  text-align: left;
  padding: 20px;
}

.won-card {
  background-color: #00aa66;
}

.lost-card {
  background-color: #cc0033;
}
.MatchDetailCard {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px;
    margin-bottom: 20px;
}

.MatchDetailCard .match-date,
.MatchDetailCard .match-venue,
.MatchDetailCard .match-result {
    margin-top: 10px;
}

.MatchDetailCard .additional-detail {
    text-align: right;
}

.MatchDetailCard .additional-detail p {
    margin-bottom: 20px;
}

.MatchSmallCard {
    padding: 10px;
    margin-bottom: 20px;
}

.MatchSmallCard .match-result {
    margin-top: 10px;
}
.TeamPage {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.TeamPage .team-name-section {
    grid-column: span 3;
    justify-self: start;
    align-self: end;
}

.TeamPage .team-name-section .team-name {
    font-size: 4rem;
    margin-bottom: 20px;
}

.TeamPage .win-loss-section {
    grid-column: span 1;
    justify-self: center;
    text-align: center;
    padding: 40px;
}

.TeamPage .match-detail-section {
    grid-column: span 4;
}

/* .TeamPage .match-small-section {
    grid-column: span 3;
} */

.TeamPage .more-link {
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
    font-size: 1.5rem;
}
.YearSelector {
    text-align: right;
    list-style-type: none;
}

.YearSelector li {
    margin-top: 10px;
    margin-bottom: 10px;
}
.MatchPage {
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-column-gap: 20px;
}

.MatchPage .year-selector {
    justify-self: end;
    padding: 20px;
}

.MatchPage .page-heading {
    margin: 20px auto;
}

.TeamTile {
    height: 150px;
    text-align: center;
    border: 1px solid white;
    padding: 20px;
}
.HomePage .team-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}


.HomePage .app-name {
    font-size: 3rem;
    margin: 30px auto;
}
